import { reactive } from '@vue/composition-api'

export const formList = () => {
   let genderList = reactive([
      { id: 'M', title: '' },
      { id: 'F', title: '' },
      { id: 'S', title: '' },
   ]);

   let questionList = reactive([
      { id: 'sq01', title: '' },
      { id: 'sq02', title: '' },
      { id: 'sq03', title: '' },
      { id: 'sq04', title: '' },
      { id: 'sq05', title: '' },
      { id: 'sq06', title: '' },
      { id: 'sq07', title: '' },
   ]);

   return { genderList, questionList };
}